import React, { Component } from 'react';
import { Textfield, Grid, Cell, Button, Card, CardTitle, CardText, CardActions, Tabs, Tab } from 'react-mdl'
import Logo from '../btwglogo.png';
import {Link} from 'react-router-dom';

class Home extends Component {

    state = {

        text: {
            recipient: '',
            textmessage: ''

        }

    }

    sendText = _ => {

        const { text } = this.state
        //pass from server
        fetch(`https://a4d99851.ngrok.io/message?recipient=${text.recipient}&textmessage=${text.textmessage}`)
        .catch(err => console.err(err))

        this.setState({ text: { ...text, textmessage: '', recipient: '' } })
        alert('your message has been sent.')
        

        


        }

    

    



render() {


    const { text } = this.state;       
    const spacer = {
          margin: 8
        }
        const textArea = {
          borderRadius: 4
        }

    return(
    
    <div>
        <div style ={{width: '100%', margin: 'auto'}}> 
        <Grid className="landing-grid">
            <Cell col={12}>
                <center><img src={Logo} alt="logo" className="logo" /></center>
            
                <div className="banner-text">
                    <center><h1>Send a Phone Call or Text Message to Anyone From Anywhere.</h1></center>
                </div>
            </Cell>
        </Grid>
        </div>


        <div style={{ width: '60%', margin: 'auto'}} >
            <Grid className="text-grid">
                <Cell col={12}>
                    <center><h2> Send Text Message </h2></center>
                        <label> Send to: </label>
                        <br />
                    <input value={text.recipient}
                            onChange={e => this.setState({ text: { ...text, recipient: e.target.value } })} />
              <div className="message-label" />
              <label> Message </label>
              <br />
              <textarea rows={10} value={text.textmessage} className="text-area"
                onChange={e => this.setState({ text: { ...text, textmessage: e.target.value } })} />
              <div/>
              <Button style={{backgroundColor:'blue', marginTop:'10px'}} raised accent onClick={this.sendText}> Send Text </Button>
              </Cell>
              
              </Grid>
            </div>

            
        
        
        
    </div>

    );

    }

}


export default Home;